import React,{Component} from 'react';
import hCss from './Header.module.css';
import CompanyName from '../CompanyName/CompanyName';
import SearchBox from '../SeachBox/SearchBoxWD';
import SearchImg from '../SeachBox/SearchImage/SearchImage';
import Menus from '../Menus/Menus';
import VMenus from '../Menus/VMenus';

class Header extends Component{
    render(){
       
        let searchList = ['Car','Land','House','Motor Bike','Food'];

        return <React.Fragment>
             <div className={hCss.mobile}>
                
                <div className={hCss.menuNlogo}>
                <div className={hCss.vmenusCont}><VMenus/> </div>
                 <div className={hCss.compName}>
                     <img src={'/logo/logo_new.png'} className={hCss.logo} style={{borderRadius:'10%'}}/>
                 </div>
                
                </div> 
               
                {this.props.nosearch===true? null :
                <React.Fragment>
                <div className={hCss.inpCont}>
                    <select className={hCss.inp}>
                        <option>Select Category</option>
                        {
                            searchList.map((val,index,)=>{
                            return <option value={val} key={val+index}>{val}</option>
                            })
                        }
                    </select> 
                </div>
               
                <div className={hCss.inpCont}>
                   <input className={hCss.inp}/>
                   <SearchImg radius='10' width='30' height='30' color='green'/>
                </div>
                </React.Fragment>
                
                }
               
             </div>
       
        <div className={hCss.mainDiv}>
           
            <div className={hCss.headContent}>
                
                <div className={hCss.compName}>
                   <img src={'/logo/logo1.png'} className={hCss.logo} style={{borderRadius:'10%'}}/>
                </div>
                
                 <div className={hCss.menusNsearch}>

                <div className={hCss.menus}>
                <Menus color='white'/>
                </div>

                {this.props.nosearch===true? null :
                <div className={hCss.search}>
                        <SearchBox width='700' height='30' imageColor='green' 
                        imageRadius='10' list={searchList}/>
                    </div>
                }  

                 </div>
            </div>
           
        </div>
        </React.Fragment>
    }
}

export default Header;