import React,{Component} from 'react';
import lCss from './Layout.module.css';
import Header from '../Header/Header';
import AdItem from '../AdsItem/AdsItem';
import AdsCategory from '../AdsCategory/AdsCategory';
import Image from '../../images/AD2.jpg';

class Layout extends Component{
    render(){
        return <div className={lCss.mainDiv}>  
             <div className={lCss.headSection}>
                 <Header/>
             </div> 
             <div className={lCss.content}>
                 <div className={lCss.category}>
                    <div className={lCss.catHeading}>We Sell Everything Legal</div>
                   <AdsCategory/>
                 </div>
            
                 <div className={lCss.hotDeals}>
                 <div className={lCss.hotDealsHeading}>Hot Deals</div>
                 <div className={lCss.displayArea}>
                 
                 <AdItem src='/images_others/1.jpg' desc='Excavator for mining and construction' rating='2'
                    disc='17' />
               
                <AdItem src='/images_others/2.jpg' desc='Import and Export' rating='4'
                    price='GHC 0.00' disc='20' />
                <AdItem src='/images_others/3.jpg' desc='Food Services' rating='3'
                    price='GHC 0.00' disc='22' />

                <AdItem src='/images_others/4.jpg' desc='Executive House for Sale' rating='2'
                    price='GHC 1,000,000.00' disc='17' />
                <AdItem src='/images_others/5.jpg' desc='Estate Property for Sales' rating='4'
                    price='GHC 570,000.00' disc='12' />
                <AdItem src='/images_others/6.jpg' desc='Your Dream Car Awaits' rating='5'
                    price='GHC 50,000.00' disc='5' />
                     
                 </div>
                 </div>
             </div>
        
             <div className={lCss.odTitle}>Other Deals</div>
             <div className={lCss.otherDeals}>
                <AdItem src='/images_others/7.jpg' desc='Excavator for mining and construction'
                        price='GHC 80,000.00'  />
                    <AdItem src='/images_others/8.jpg' desc='Import and Export' rating='4'
                        price='GHC 0.00' disc='20' />
                    <AdItem src='/images_others/9.jpg' desc='Food Services' rating='3'
                        price='GHC 0.00' disc='22' />
                    <AdItem src='/images_others/10.jpg' desc='Executive House for Sale' rating='2'
                        price='GHC 1,000,000.00' disc='17' />
                    <AdItem src='/images_others/11.jpg' desc='Estate Property for Sales' rating='4'
                        price='GHC 570,000.00' disc='12' />
                    <AdItem src='/images_others/12.jpg' desc='Your Dream Car Awaits' rating='5'
                        price='GHC 50,000.00' disc='5' /> 
                     <AdItem src='/images_others/13.jpg' desc='Executive House for Sale' rating='2'
                        price='GHC 1,000,000.00' disc='17' />
                    <AdItem src='/images_others/15.jpg' desc='Estate Property for Sales' rating='4'
                        price='GHC 570,000.00' disc='12' />
                    <AdItem src='/images_others/16.jpg' desc='Your Dream Car Awaits' rating='5'
                        price='GHC 50,000.00' disc='5' />     
             </div>
        
        </div>
    }
}

export default Layout;