import React from 'react';
import tCss from './TextOnImage.module.css';

class TextOnImage extends React.Component{
    render(){
           return <div className={tCss.mainDiv}>
               <div className={tCss.textDiv} style={this.props.textStyle}>
                   {this.props.children}
               </div>
           </div>
    }
}

export default TextOnImage;