import React from 'react';
import Header from '../Header/Header';
import sCss from './SelltoUs.module.css';

class SelltoUs extends React.Component{
    render(){

        let searchList = ['Car','Land','House','Motor Bike','Food'];
        return <div style={{backgroundColor:'green'}}>
        <Header nosearch={true}/>
       
      <div className={sCss.content}>
          <div className={sCss.question}>What do you have for us?</div>
          <form method='post' id='contactForm'  onSubmit={(event)=>this.checkNsubmit(event,this)} >
           
           <div className={sCss.cformInputs}>
           <label className={sCss.hidden} id='mailStatus'>Server could not send email. Try sometime later.</label>
           <select className={sCss.inp}>
                        <option>Select Category</option>
                        {
                            searchList.map((val,index,)=>{
                            return <option value={val} key={val+index}>{val}</option>
                            })
                        }
                    </select> 
           <textarea className={sCss.textarea} placeholder='Describe the item here.'>
           </textarea>
           <input className={sCss.inp} placeholder='Price eg. GHC 50,000.00'/>
           
        
           <div className={sCss.addPicDiv}>
           <div>Add picture(s):</div> 
           <div><input type="file" id="selImage" name="selImage"/></div> 
           </div>
        

           <button id='sendBtn'>Send</button>
           </div>

          </form>
          </div>

          </div>
    }
}


export default SelltoUs;