import React,{Component} from 'react';
import cmCss from './CompanyName.module.css';

class CompanyName extends Component{
    render(){
        return <React.Fragment>
           <div className={cmCss.mainDiv}>
               
               <div className={cmCss.nameBox}>
                <div className={cmCss.name}>
                    <div className={cmCss.name1}>Zainab</div>
                    <div className={cmCss.name2}>Swallow</div>
                </div>
                <div className={cmCss.copyRight}>
                    &trade;
                </div>
               </div>
               
           </div>
        </React.Fragment>
    }
}

export default CompanyName;