import React, {Component} from 'react';
import adCss from './AdsItem.module.css';
import {NavLink,Redirect} from 'react-router-dom'
import TextOnImage from '../TextOnImage/TextOnImage';

class AdsItem extends Component{
    render(){
        return <div className={adCss.mainDiv}>
            <NavLink className={adCss.navlink} exact to={{
                pathname: '/addetail',
                itemDetail:{
                    src:this.props.src,
                    desc:this.props.desc,
                    price:this.props.price===undefined? 'GHC 0.00' : this.props.price
                }
            }}>

                <div className={adCss.imgDiv}>
                    <img src={this.props.src} alt='image' className={adCss.img}/>
                </div>
            
                <div className={adCss.desc}>{this.props.desc}</div> 
            
                <div className={adCss.price}>
                    {this.props.price===undefined? 'GHC 0.00' : this.props.price}</div>
            </NavLink>
           
        </div>
    }
   
    displayRating(num){
       let str = '';
       for(let i=0; i<num; i++)
       str+='*';
       return str;
    }                 
}

export default AdsItem;