import React,{Component} from 'react';
import siCss from './SearchImage.module.css';

class SearchImage extends Component{
    render(){
        let cx=this.props.width/2; 
        let cy = this.props.height/2;
        let halfRadius = this.props.radius/2;
       return <div className={siCss.mainImgDiv}>
           <div className={siCss.svgDiv} style={{borderColor:this.props.color}}>
            <svg width={this.props.width+'px'} height={this.props.height+'px'}>
                <circle cx={cx+'px'} cy={cy+'px'} 
                r={this.props.radius+'px'} stroke={this.props.color} 
                stroke-width="1" fill="white" />
                image for browsers that don't support svg

                  {/** draw line from circumference to end of box */}
            <line x1={cx+3+halfRadius+'px'} y1={cy+3+halfRadius+'px'} 
            x2={this.props.width} y2={this.props.width} style={{stroke:this.props.color,strokeWidth:1}} /> 
            </svg>
          
           </div>
           
        </div>

    }
}

export default SearchImage;