import React,{Component} from 'react';
import sCss from './SearchBox.module.css';
import SearchImage from './SearchImage/SearchImage';

class SearchBox extends Component{
    render(){
        return <div className={sCss.compDiv} 
        style={{height:this.props.height+'px',width:this.props.width+'px'}}>
            <input type='text' id='search_txtbx' className={sCss.tbx}
            style={{borderColor:this.props.imageColor}}/>
            <div className={sCss.imgBtnDiv}>
              <SearchImage width={this.props.height} height={this.props.height} 
              radius={this.props.imageRadius} color={this.props.imageColor}/>
            </div>
           
        </div>
    }
} 

export default SearchBox;