import React from 'react';
import aCss from './AdDetail.module.css';
import Header from '../Header/Header';
import util from 'util';


class AdDetail extends React.Component{
    render(){
        let imginfo = this.props.location.itemDetail; 
        return <div className={aCss.main}>
        <div>
            <Header nosearch={true}/>
        </div>
        <div className={aCss.content}>
            <div className={aCss.video}>
                <video width="100%" height="auto" controls>
                 <source  src="movie.mp4" type="video/mp4"/>
                 <source src="movie.ogg" type="video/ogg"/>
                 Your browser does not support the video tag.
                 Please upgrade the browser and try again.
                </video>
            </div>

            <div className={aCss.imgGal}>
              <div className={aCss.desc}>{imginfo.desc}</div>
              <div className={aCss.desc}>{imginfo.price}</div>
             <div className={aCss.imgDiv}>
                 <img src={imginfo.src} alt='' className={aCss.img}/>
             </div>
             <div className={aCss.btns}>
                 <input type="button" value='<<' className={aCss.btn}/>
                 <input type="button" value='>>' className={aCss.btn}/>
             </div>
            </div>
        </div>
        </div>
    }
}

export default AdDetail;