import React,{Component} from 'react';
import sCss from './SearchBox.module.css';
import SearchImage from './SearchImage/SearchImage';

class SearchBoxWD extends Component{
    render(){
        return <div className={sCss.compDiv} 
        style={{height:this.props.height+'px',
        width:this.props.width+'px'}}>

            <select id='selDD' className={sCss.dd} 
            style={{borderColor:this.props.imageColor,color:this.props.imageColor}}>
                <option selected disabled>Search From</option>
                {this.props.list.map((item,index,)=>{
                    return <option key={item+index} value={item}>{item}</option>
                })}
            </select>

            <div className={sCss.tbxNbtn}>
                <input type='text' id='search_txtbx' className={sCss.tbxDD}
                style={{borderColor:this.props.imageColor,color:this.props.imageColor}}/>

                <div className={sCss.imgBtnDivDD}>
                <SearchImage width={this.props.height} height={this.props.height} 
                radius={this.props.imageRadius} color={this.props.imageColor}/>
                </div>
            </div>
           
           
        </div>
    }
} 

export default SearchBoxWD;