import React from 'react';
import aCss from './AdsCategory.module.css';
import TextOnImage from '../TextOnImage/TextOnImage';

class AdsCategory extends React.Component{
    state ={curItemIndex:0,max:6,imgs:[],intervalHandle:null};
    componentDidMount(){
    
     let imgArr = [];
     for(let i=1; i<=this.state.max;i++)
        imgArr.push('/images/'+i+'.jpg'); 
      this.setState({imgs:imgArr});

      this.showImage();
    
    }
    
    componentWillUnmount(){
      clearInterval(this.state.intervalHandle);
    }

    render(){
     
        return <div className={aCss.mainDiv}>
              <div className={aCss.imgDiv}>
               {
                 this.state.imgs.map((img,index,)=>{
                   let clsname = aCss.adimg;
                   if((index+1)%4===0)
                     clsname+=' '+aCss.animDown;
                   else if((index+1)%3===0)
                     clsname+=' '+aCss.animUp;
                   else if((index+1)%2===0)
                     clsname+=' '+aCss.animLeft;
                    else
                       clsname+=' '+aCss.animRight;

                   return   <img key={index+img} style={{display:'none'}}  src={img}   className={clsname}/>
                 })
               }
              
              </div>
              <div className={aCss.textDiv}>
                
              </div>
             

        </div>
    }

    showImage(){
      let handle = setInterval(()=>
      {
         let elems = document.getElementsByClassName(aCss.adimg);
         for(let i=0; i<elems.length;i++)
           elems[i].style.display='none';
        let curindex = this.state.curItemIndex;
        if(curindex>=this.state.max)
          curindex=0;
        else 
           curindex++;
         elems[curindex%this.state.max].style.display='flex';
         
         this.setState({curItemIndex:curindex});
      },3000);

      this.setState({intervalHandle:handle});
      

    }
}

export default AdsCategory;